export function getBetweenRandom (min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min)
}

export function shuffle (arr) {
  let _arr = arr.slice()
  for (let i = 0; i < _arr.length; i++) {
    let j = getBetweenRandom(0, i)
    let t = arr[i]
    _arr[i] = _arr[j]
    _arr[j] = t
  }
  return _arr
}





